/**
 * Tooltip
 */

[ulg-tooltip-admin] {
	position: relative; 
}

	[ulg-tooltip-admin]::before,
	[ulg-tooltip-admin]::after {
		text-transform: none;
		font-size: 13px;
		font-weight: 500;
		line-height: 1;
		user-select: none;
		pointer-events: none;
		position: absolute;
		display: none;
		opacity: 0;
	}

	[ulg-tooltip-admin]::before {
		content: '';
		border: 5px solid transparent;
		z-index: 1001;
	}

	[ulg-tooltip-admin]::after {
		width: 100%;
		content: attr(ulg-tooltip-admin);
		text-align: center;
		min-width: 80px;
		max-width: 21em;
		/*white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;*/
		padding: 4px 10px 6px 10px;
		line-height: 1.4;
		border-radius: 4px;
		box-shadow: 0 1em 2em -.5em rgba(0, 0, 0, 0.35);
		background: #333;
		color: #fff;
		z-index: 1000;
	}

	[ulg-tooltip-admin][ult-flow-admin^="left"]::after {
		min-width: 80px;
	}

	[ulg-tooltip-admin]:hover::before,
	[ulg-tooltip-admin]:hover::after {
		display: block;
	}

	[ulg-tooltip-admin='']::before,
	[ulg-tooltip-admin='']::after {
		display: none !important;
	}

	[ulg-tooltip-admin]:not([ult-flow-admin])::before,
	[ulg-tooltip-admin][ult-flow-admin^="up"]::before {
		bottom: calc(100% + 5px);
		border-bottom-width: 0;
		border-top-color: #333;
	}
	[ulg-tooltip-admin]:not([ult-flow-admin])::after,
	[ulg-tooltip-admin][ult-flow-admin^="up"]::after {
		bottom: calc(100% + 10px);
	}
	[ulg-tooltip-admin]:not([ult-flow-admin])::before,
	[ulg-tooltip-admin]:not([ult-flow-admin])::after,
	[ulg-tooltip-admin][ult-flow-admin^="up"]::before,
	[ulg-tooltip-admin][ult-flow-admin^="up"]::after {
		left: 50%;
		transform: translate(-50%, -.5em);
	}

	[ulg-tooltip-admin][ult-flow-admin^="down"]::before {
		top: 100%;
		border-top-width: 0;
		border-bottom-color: #333;
	}
	[ulg-tooltip-admin][ult-flow-admin^="down"]::after {
		top: calc(100% + 5px);
	}
	[ulg-tooltip-admin][ult-flow-admin^="down"]::before,
	[ulg-tooltip-admin][ult-flow-admin^="down"]::after {
		left: 50%;
		transform: translate(-50%, .5em);
	}
	[ulg-tooltip-admin][ult-flow-admin^="left"]::before {
		top: 50%;
		border-right-width: 0;
		border-left-color: #333;
		left: calc(0em - 10px);
		transform: translate(-.5em, -50%);
	}
	[ulg-tooltip-admin][ult-flow-admin^="left"]::after {
		top: 50%;
		right: calc(100% + 10px);
		transform: translate(-.5em, -50%);
	}
	[ulg-tooltip-admin][ult-flow-admin^="right"]::before {
		top: 50%;
		border-left-width: 0;
		border-right-color: #333;
		right: calc(0em - 10px);
		transform: translate(.5em, -50%);
	}
	[ulg-tooltip-admin][ult-flow-admin^="right"]::after {
		top: 50%;
		left: calc(100% + 10px);
		transform: translate(.5em, -50%);
	}

	[ulg-tooltip-admin]:not([ult-flow-admin]):hover::before,
	[ulg-tooltip-admin]:not([ult-flow-admin]):hover::after,
	[ulg-tooltip-admin][ult-flow-admin^="up"]:hover::before,
	[ulg-tooltip-admin][ult-flow-admin^="up"]:hover::after,
	[ulg-tooltip-admin][ult-flow-admin^="down"]:hover::before,
	[ulg-tooltip-admin][ult-flow-admin^="down"]:hover::after {
		animation: ulg-tooltip-admins-vert 300ms ease-out forwards;
	}

	[ulg-tooltip-admin][ult-flow-admin^="left"]:hover::before,
	[ulg-tooltip-admin][ult-flow-admin^="left"]:hover::after,
	[ulg-tooltip-admin][ult-flow-admin^="right"]:hover::before,
	[ulg-tooltip-admin][ult-flow-admin^="right"]:hover::after {
		animation: ulg-tooltip-admins-horz 300ms ease-out forwards;
	}

	@keyframes ulg-tooltip-admins-vert {
		to {
			opacity: .9;
			transform: translate(-50%, 0);
		}
	}

	@keyframes ulg-tooltip-admins-horz {
		to {
			opacity: .9;
			transform: translate(0, -50%);
		}
	}
