.ulgm-sidebar-featured-item {
  position: relative;

  .ulgm-sidebar-featured-item__close {
	$class: '.ulgm-sidebar-featured-item__close';
	$x_width: 8px;

	width:  $x_width + 4px;
	height: $x_width + 4px;

	position: absolute;
	right: -12px;
	top: 50%;
	transform: translateY(-50%);

	border-radius: 50%;

	cursor: pointer;

	transition: background 150ms ease-in-out;

	&-icon {
	  width: $x_width;
	  height: $x_width;

	  top: 50%;
	  left: 50%;
	  transform: translate(-50%,-50%);
	  position: absolute;

	  &:before {
		transform: translate(-50%,-50%) rotate(-45deg);
	  }

	  &:after {
		transform: translate(-50%,-50%) rotate(45deg);
	  }

	  &:before,
	  &:after {
		content: '';

		width: $x_width;
		height: 1px;

		background: #7c7e81;

		position: absolute;

		top: 50%;
		left: 50%;

		transform-origin: center;

		transition: background 150ms ease-in-out;
	  }
	}

	&:hover {
	  background: #7c7e81;

	  & #{ $class }-icon {
		&:before,
		&:after {
		  background: #fff;
		}
	  }
	}
  }
}

.ulgm-sidebar-featured-item-container {
  &--loading {
	@include placeholder-shimmer();
  }
}
