/**
 * Tabs
 */

.ulgm nav.nav-tab-wrapper {
	margin: 0 0 20px;
}

/**
 * Admin tabs
 */

.ulgm-admin-nav {
	width: 100%;
}

	.ulgm-admin-nav-items {
		width: 100%;
	}

	.ulgm-admin-nav-social-icons {
		position: relative;
		padding-top: 5px;
		padding-left: 10px;

		display: inline-flex;
		float: right;
	}

		.ulgm-admin-nav-social-icon {
			position: relative;
	
			width: 22px;
			height: 22px;

			font-size: 12px;
			color: #f1f1f1;

			background: #939393;
			border-radius: 50%;

			cursor: pointer;

			transition: color 150ms ease-in-out, background 150ms ease-in-out;
		}

			.ulgm-admin-nav-social-icon:hover,
			.ulgm-admin-nav-social-icon:focus,
			.ulgm-admin-nav-social-icon:active {
				border: 0;
				box-shadow: none;
				background: #0790e8;
				color: #f1f1f1;
			}

			.ulgm-admin-nav-social-icon span {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%,-50%);
			}

		.ulgm-admin-nav-social-icon:not(:last-child) {
			margin-right: 8px;
		}

		.ulgm-admin-nav-social-icon--facebook {
			font-size: 23px;
			color: #939393;
			background: transparent;
		}

			.ulgm-admin-nav-social-icon--facebook:hover,
			.ulgm-admin-nav-social-icon--facebook:active,
			.ulgm-admin-nav-social-icon--facebook:focus {
				background: transparent;
				color: #0790e8;
			}

		.ulgm-admin-nav-social-icon--linkedin span {
			margin-left: 1px;
		}

	@media screen and (max-width: 1024px){
		.ulgm-admin-nav-social-icons {
			float: left;
		}

		.ulgm-admin-nav-items > a {
			border-bottom: 1px solid #ccc;
		}

		.ulgm-admin-nav-social-icons,
		.ulgm-admin-nav-items > a {
			margin: 10px 10px 0 0;
		}

		.ulgm-header .nav-tab-wrapper,
		.ulgm-header .wrap h2.nav-tab-wrapper,
		.ulgm-header h1.nav-tab-wrapper {
			border-bottom: 0;
		}
	}