.uo-ulgm-admin .uo-checkbox,
.uo-ulgm-admin .uo-radio {
	cursor: pointer;
	width: 100%;
	display: flex;
}

.uo-ulgm-admin .uo-checkbox:after,
.uo-ulgm-admin .uo-radio:after {
	content: '';
	display: table;
	clear: both;
}

.uo-ulgm-admin .uo-checkbox .uo-label,
.uo-ulgm-admin .uo-radio .uo-label {
	padding-left: 8px;
}

.uo-ulgm-admin .uo-checkbox input,
.uo-ulgm-admin .uo-radio input {
	display: none;
}

.uo-ulgm-admin .uo-checkbox .uo-label,
.uo-ulgm-admin .uo-radio .uo-label,
.uo-ulgm-admin .uo-checkbox .uo-checkmark,
.uo-ulgm-admin .uo-radio .uo-checkmark {
	float: left;
}

.uo-ulgm-admin .uo-checkbox .uo-checkmark,
.uo-ulgm-admin .uo-radio .uo-checkmark {
	width: 16px;
	height: 16px;

	display: inline-block;
	cursor: pointer;

	border-color: #b7b7b7;
	border-width: 1px;
	border-style: solid;

	-webkit-border-radius: 3px;
	-moz-border-radius: 3px;
	-ms-border-radius: 3px;
	-o-border-radius: 3px;
	border-radius: 3px;

	-webkit-box-shadow: 0 1px 2px 0 rgba(0, 0, 0, .3);
	box-shadow: 0 1px 2px 0 rgba(0, 0, 0, .3);

	transition-duration: 100ms;
	transition-timing-function: linear;
	transition-property: color, border-color;

	background: #f9fafb;
	background: -moz-linear-gradient(top, #f9fafb 0%, #fefefe 100%);
	background: -webkit-linear-gradient(top, #f9fafb 0%, #fefefe 100%);
	background: linear-gradient(to bottom, #f9fafb 0%, #fefefe 100%);
}

.uo-ulgm-admin .uo-radio .uo-checkmark {
	-webkit-border-radius: 50%;
	-moz-border-radius: 50%;
	-ms-border-radius: 50%;
	-o-border-radius: 50%;
	border-radius: 50%;
}

.uo-ulgm-admin .uo-checkbox .uo-checkmark,
.uo-ulgm-admin .uo-radio .uo-checkmark {
	position: relative;
}

.uo-ulgm-admin .uo-checkbox:hover input ~ .uo-checkmark,
.uo-ulgm-admin .uo-checkbox input:checked ~ .uo-checkmark,
.uo-ulgm-admin .uo-radio:hover input ~ .uo-checkmark,
.uo-ulgm-admin .uo-radio input:checked ~ .uo-checkmark {
	border-color: #0085ba;
	color: #0085ba;
}

.uo-ulgm-admin .uo-checkbox input ~ .uo-checkmark:after,
.uo-ulgm-admin .uo-radio input:checked ~ .uo-checkmark:after {
	content: '';
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	opacity: 0;
	transition-duration: 100ms;
	transition-timing-function: linear;
	transition-property: opacity;
}

.uo-ulgm-admin .uo-radio input:checked ~ .uo-checkmark:after {
	background: #0085ba;
}

.uo-ulgm-admin .uo-checkbox input ~ .uo-checkmark:after {
	border-style: solid;
	border-top: 0;
	border-right: 0;
	border-left-width: 3px;
	border-bottom-width: 3px;
	transform: translate(-50%,-50%) rotate(-45deg);
	width: 7px;
	height: 3px;
	top: calc(50% - 1px);
	left: 50%;
}

.uo-ulgm-admin .uo-radio input ~ .uo-checkmark:after {
	width: calc(100% - 8px);
	height: calc(100% - 8px);
	-webkit-border-radius: 50%;
	-moz-border-radius: 50%;
	-ms-border-radius: 50%;
	-o-border-radius: 50%;
	border-radius: 50%;
}

.uo-ulgm-admin .uo-checkbox input:checked ~ .uo-checkmark:after,
.uo-ulgm-admin .uo-radio input:checked ~ .uo-checkmark:after {
	opacity: 1;
}

.uo-ulgm-admin .uo-radio ~ .uo-radio {
	margin-top: 7px;
}

