.uo-ulgm-admin {
	.uo-admin-block {
		.uo-admin-accordion {
			width: 100%;

			.uo-admin-accordion-item {
				width: 100%;

				border-bottom: 1px solid #e3e3e3;

				&:first-child {
					border-top: 1px solid #e3e3e3;
				}

				.uo-admin-accordion-item__header {
					font-size: 16px;
					font-weight: 500;

					position: relative;

					cursor: pointer;

					padding: 12px 0;

					transition: color 300ms ease-in-out;

					&:before {
						@include icons-font;

						content: $icon-angle-down;

						font-size: 16px;

						position: absolute;
						top: 50%;
						right: 0;
						transform: translateY(-50%);

						transition: transform 500ms ease-in-out;
					}
				}

				&--open {
					.uo-admin-accordion-item__header {
						color: #0085ba;

						&:before {
							color: #444;

							transform: translateY(-50%) rotate(180deg);
						}
					}
				}

				.uo-admin-accordion-item__content {
					display: none;

					padding-bottom: 15px;
				}
			}
		}
	}
